<template>
  <div class="asset"
    v-if=assetExists>
    <div class="layer layer-1">
      <h1 class="title">Asset</h1>
    </div>
    <div class="layer layer-2">
      <img class="asset-image" :src=assetData.path />
    </div>
    <div class="layer layer-3">
      <div class="container container-asset">
        
      </div>
    </div>
    <div class="layer layer-5">
      <router-link :to="`user/${ assetData.owner.userName }`"
        class="owner-group container container-user">
        <img class="owner-image" :src=assetData.owner.profileAsset.path />
        <div class="owner-info">
          <div class="owner-info-name">{{ assetData.owner.name }}</div>
          <div class="owner-info-username">{{ assetData.owner.userName }}</div>
        </div>
      </router-link>
      <button class="form-button form-button-user follow-button"
        v-if=isAuthenticated
        :class="{ 'form-button-inverted': currentUserFollowsAuthor }">
        {{  currentUserFollowsAuthor ? 'Following' : 'Follow' }}
      </button>
    </div>
  </div>
  <div class="asset asset-error" v-else>{{ assetError }}</div>
</template>

<script>
import marked from 'marked'
import { mapState } from 'vuex'

export default {
  name: 'asset',

  beforeMount() {
  
    this.populatePost();
  },
  watch: {
    '$route'() {
      
      this.populatePost();
    }
  },

  data: () => ({

    assetExists: true,
    assetError: '',
    
    assetContent: '',
    appreciationInfo: '',
    assetData: {
      markdownContent: '',
      title: 'Loading...',
      idTitle: '',
      views: 0,
      appreciations: 0,
      writtenDate: '',
      editedDate: '',
      keywords: [],
      
      owner: {
        profileAsset: {
          path: ''
        },
        userName: '',
        name: '',
        description: '',
        privilegeStatus: ''
      }
    }

  }),

  computed: {
    ...mapState([ 'currentUser', 'isAuthenticated' ]),
    
    currentUserAppreciatesPost() {
    
      return this.currentUser.appreciatedPosts.find( asset =>
        asset.idTitle === this.$route.params.assetid &&
        asset.owner.userName === this.$route.params.username );
    },
    currentUserFollowsAuthor() {
      return this.currentUser.follows.find( user =>
        user.userName === this.$route.params.username ) 
    }
  },


  methods: {
    populatePost() {
      
      this.$store.dispatch( 'populatePost', {
        userName: this.$route.params.username,
        assetId: this.$route.params.assetid

      }).then( assetData => {

        const createdDate = new Date( assetData.createdAt );
        assetData.writtenDate = [ createdDate.getDate(), createdDate.getMonth() + 1, createdDate.getFullYear() ].join('/');
        
        const editedDate = new Date( assetData.editedAt );
        assetData.editedDate = [ editedDate.getDate(), editedDate.getMonth() + 1, editedDate.getFullYear() ].join('/');

        this.assetData = assetData;
        this.assetContent = marked( this.assetData.markdownContent );

      }).catch( error => {

        this.assetExists = false;
        this.assetError = error;

      
      })
    },
    appreciatePost() {

      if( this.currentUserAppreciatesPost )
        return false;

      this.$store.dispatch( 'appreciatePost', {
        assetId: this.$route.params.assetid,
        userName: this.$route.params.username

      }).then( ( err ) => {
        
        if( err )
          return this.setAppreciationInfo( err );
  
        this.currentUser.appreciatedPosts.push( this.assetData );
        this.setAppreciationInfo( '' );
      });
    },
    setAppreciationInfo( message ) {
      
      this.appreciationInfo = message;

    }
  }
}
</script>

<style scoped>

.asset-error {
  font-size: 20px;
  color: #ccc;
  text-align: center;
  width: 100%;
  margin-top: 40px;
}
.layer {
  margin-top: 20px;
}
.layer-1 {
  margin-top: 50px;
}
.asset-title {
  text-align: center;
  color: #eee;
  margin: auto;
  font-size: 34px;
}
.asset-content {
  padding: 18px;
  width: 612px;
  margin: auto;
  background-color: #f8f8f8;
}
.layer-3 {
  display: flex;
  width: 612px;
  margin-left: calc( 50% - 612px/2 );
  justify-content: space-around;
  text-align: center;
  color: #bbb;
  font-size: 13px;
}
.layer-4 {
  margin-top: 50px;
}
.form-info {
  display: block;
  text-align: center;
}
.appreciate-button {
  width: 350px;
  display: block;
}
.layer-5 {
  width: 320px; 
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
}
.owner-group {
  display: flex;
}
.owner-image {
  width: 64px;
  height: 64px;
  border-radius: 100%;
}
.owner-info {
  margin-top: 9px;
  margin-left: 13px;
}
.owner-info-name {
  color: #bbb;
  font-size: 13px;
}
.owner-info-username {
  color: #ccc;
  font-size: 24px;
}
.follow-button {
  width: 106px;
  margin-right: 0;
}
</style>



